import React, { useState } from 'react';
import { TabPanel } from '@wordpress/components';
import ViewSettings from './ViewSettings';
import FieldSettings from './FieldSettings';
import SettingsFlyout from './SettingsFlyout';
const ViewSidebar = ( props ) => {
	const [ openFlyout, setOpenFlyout ] = useState( false );
	const [ flyoutType, setFlyoutType ] = useState( '' ); // searchSettings, filterSettings, sortSettings

	// Settings Flyout for Search / Filter / Sort
	const openFlyoutHandler = ( flyoutType ) => {
		setOpenFlyout( true );
		setFlyoutType( flyoutType );
	};

	const activeFieldSettingTabClass = !! props.viewSettingsProps
		.openFieldSettingsPanel
		? 'is_active_side_tab'
		: '';
	const activeViewSettingTabClass = ! props.viewSettingsProps
		.openFieldSettingsPanel
		? 'is_active_side_tab'
		: '';

	return (
		<>
			<TabPanel
				className="edit-post-sidebar"
				activeClass="placeholder_active"
				onSelect={ ( tabName ) => {
					tabName === 'viewSettingTab'
						? props.viewSettingsProps.setOpenFieldSettingsPanel(
								false
						  )
						: props.viewSettingsProps.setOpenFieldSettingsPanel(
								true
						  );
				} }
				tabs={ [
					{
						name: 'viewSettingTab',
						title: 'View Settings',
						className: activeViewSettingTabClass,
					},
					{
						name: 'fieldSettingTab',
						title: 'Field Settings',
						className: activeFieldSettingTabClass,
					},
				] }
			>
				{ ( tab ) => {
					if ( props.viewSettingsProps.openFieldSettingsPanel ) {
						return (
							<FieldSettings
								fieldSettingsProps={ props.fieldSettingsProps }
								openFlyoutHandler={ openFlyoutHandler }
							/>
						);
					} else {
						return (
							<ViewSettings
								viewSettings={ props.viewSettingsProps }
								openFlyoutHandler={ openFlyoutHandler }
							/>
						);
					}
				} }
			</TabPanel>
			{ openFlyout && (
				<SettingsFlyout
					viewSettings={ props.viewSettingsProps }
					fieldSettingsProps={ props.fieldSettingsProps }
					setOpenFlyout={ setOpenFlyout }
					flyoutType={ flyoutType }
				></SettingsFlyout>
			) }
		</>
	);
};
export default ViewSidebar;
