import React, { useState } from 'react';
import {
	Button,
	TextControl,
	SelectControl,
	Popover,
} from '@wordpress/components';
import { isEmpty, cloneDeep } from 'lodash';
import MergeTagList from './MergeTagList';
import {
	DisabledFilterSortFields,
	FieldWithValues,
	upgradeLink,
} from '../../../Constants';

import './index.css';

const FilterFlyout = ( props ) => {
	const [ popoverAnchor, setPopoverAnchor ] = useState();
	const [ isVisible, setIsVisible ] = useState( false );
	const [ conditionGroupIndex, setConditionGroupIndex ] = useState( 0 );
	const [ conditionRowIndex, setConditionRowIndex ] = useState( 0 );
	const openMergeTagPopover = ( e, groupIndex, rowIndex ) => {
		setPopoverAnchor( e.target );
		setConditionGroupIndex( groupIndex );
		setConditionRowIndex( rowIndex );
		setIsVisible( true );
	};

	let savedFilterConditionsGroups = '';

	if ( typeof props.viewSettingsProps.viewSettings.filters !== 'undefined' ) {
		savedFilterConditionsGroups =
			props.viewSettingsProps.viewSettings.filters;
	}

	const isPro = props.viewSettingsProps.isPro;

	const formFields = props.viewSettingsProps.formFields;
	const { _view_config } = window;
	const view_config = JSON.parse( _view_config );
	/**
	 * Get Form Fields to show in dropdown
	 */
	const formFieldOptions = Object.keys( formFields ).reduce(
		( result, key ) => {
			if (
				!DisabledFilterSortFields.includes(
					formFields[ key ].fieldType
				)
			) {
				result.push(
					<option
						key={`frmfieldoption${key}`}
						value={formFields[ key ].id}
					>
						{formFields[ key ].label}
					</option>
				);
			}
			return result;
		},
		[]
	);

	const addMergeTagValue = ( e, groupIndex, rowIndex, tag ) => {
		props.viewSettingsProps.addMergeTagValue(
			'viewSettings',
			'filters',
			groupIndex,
			rowIndex,
			tag
		);
		setIsVisible( false );
	};

	const saveConditionalLogicDropdownField = (
		value,
		groupIndex,
		rowIndex,
		valueType
	) => {
		if ( value === 'custom' || valueType === 'resetValueType' ) {
			props.viewSettingsProps.saveConditionalLogicField(
				value,
				'viewSettings',
				'filters',
				'valueType',
				groupIndex,
				rowIndex
			);
		} else {
			props.viewSettingsProps.saveConditionalLogicField(
				value,
				'viewSettings',
				'filters',
				'value',
				groupIndex,
				rowIndex
			);
		}
	};

	// Loop through filter Rows
	let filterConditionsGroups = '';
	if ( !isEmpty( savedFilterConditionsGroups ) ) {
		filterConditionsGroups = savedFilterConditionsGroups.map(
			( group, groupIndex ) => {
				let filterRows = '';
				if ( !isEmpty( group ) ) {
					filterRows = group.map( ( filter, rowIndex ) => {
						if (
							typeof formFields[ filter.field ] === 'undefined'
						) {
							props.viewSettingsProps.removeConditionalLogicRow(
								'viewSettings',
								'filters',
								groupIndex,
								rowIndex
							);
							return '';
						}
						let mergeTagsIcon = '';
						if (
							view_config.prefix !== 'cf7' &&
							formFields[ filter.field ].fieldType !==
							'approvalStatus' &&
							( !FieldWithValues.includes(
								formFields[ filter.field ].fieldType
							) ||
								filter.valueType === 'custom' )
						) {
							mergeTagsIcon = (
								<div>
									<i
										onClick={( e ) =>
											openMergeTagPopover(
												e,
												groupIndex,
												rowIndex
											)
										}
										className="fas fa-bars mergeTagsIcon"
									>
										{' '}
									</i>
								</div>
							);
						} else {
							mergeTagsIcon = '';
						}
						let compareOperators = [
							<option key="compare1" value="=">
								equal to
							</option>,
						];

						if ( filter.field !== 'approvalStatus' ) {
							compareOperators.push(
								<option key="compare2" value="!=">
									NOT equal to
								</option>
							);
							compareOperators.push(
								<option key="compare3" value=">=">
									greater than or equal to
								</option>
							);
							compareOperators.push(
								<option key="compare4" value="<=">
									less than or equal to
								</option>
							);
						}
						if (
							filter.field !== 'entryDate' &&
							filter.field !== 'approvalStatus'
						) {
							compareOperators.push(
								<option key="compare5" value=">">
									greater than
								</option>
							);
							compareOperators.push(
								<option key="compare6" value="<">
									less than
								</option>
							);
							compareOperators.push(
								<option key="compare7" value="LIKE">
									contains
								</option>
							);
						}

						let FilterValueField = [];

						FilterValueField.push(
							<TextControl
								className="filterValue"
								key={`filterValue${rowIndex}`}
								onChange={( value ) =>
									props.viewSettingsProps.saveConditionalLogicField(
										value,
										'viewSettings',
										'filters',
										'value',
										groupIndex,
										rowIndex
									)
								}
								value={filter.value}
								type="text"
							/>
						);

						if (
							formFields[ filter.field ].fieldType ===
							'approvalStatus'
						) {
							FilterValueField = (
								<SelectControl
									className="filterValue"
									value={filter.value}
									onChange={( value ) =>
										saveConditionalLogicDropdownField(
											value,
											groupIndex,
											rowIndex
										)
									}
								>
									<option
										key={`selectapprovedStatus${rowIndex}`}
										value=""
									>
										Select
									</option>
									<option
										key={`approvedStatus${rowIndex}`}
										value="approved"
									>
										Approved
									</option>
									<option
										key={`unapprovedStatus${rowIndex}`}
										value="unapproved"
									>
										Unapproved
									</option>
								</SelectControl>
							);
						} else if (
							FieldWithValues.includes(
								formFields[ filter.field ].fieldType
							) &&
							filter.valueType !== 'custom'
						) {
							FilterValueField = (
								<SelectControl
									className="filterValue"
									value={filter.value}
									onChange={( value ) =>
										saveConditionalLogicDropdownField(
											value,
											groupIndex,
											rowIndex
										)
									}
								>
									<option
										key={`selectapprovedStatus${rowIndex}`}
										value=""
									>
										Select
									</option>
									{Object.keys(
										formFields[ filter.field ].values
									).map( ( fieldValue, ind ) => {
										return (
											<option
												key={`frmfieldvalue${ind}`}
												value={fieldValue}
											>
												{
													formFields[ filter.field ]
														.values[ fieldValue ]
												}
											</option>
										);
									} )}
									<option
										key={`customfilterVal${rowIndex}`}
										value="custom"
									>
										Custom Value
									</option>
								</SelectControl>
							);
						}

						let resetCustomValue = [];
						if ( filter.valueType === 'custom' ) {
							FilterValueField.push(
								<div
									key={`resetCustomValue${rowIndex}`}
									className="resetValueTypeCont"
								>
									<button
										key={`resetcustomfilterVal${rowIndex}`}
										value=""
										className="resetValueType"
										onClick={() =>
											saveConditionalLogicDropdownField(
												'',
												groupIndex,
												rowIndex,
												'resetValueType'
											)
										}
									>
										back
									</button>
								</div>
							);
						}

						return (
							<div
								key={`filterRow${rowIndex}`}
								className=" condition_field_row"
							>
								<SelectControl
									onChange={( value ) =>
										props.viewSettingsProps.saveConditionalLogicField(
											value,
											'viewSettings',
											'filters',
											'field',
											groupIndex,
											rowIndex
										)
									}
									className="filter_field_dropdown"
									value={filter.field}
								>
									<optgroup label="Form Fields">
										{formFieldOptions}
									</optgroup>
								</SelectControl>
								is
								<SelectControl
									onChange={( value ) =>
										props.viewSettingsProps.saveConditionalLogicField(
											value,
											'viewSettings',
											'filters',
											'is',
											groupIndex,
											rowIndex
										)
									}
									value={filter.is}
									className="filter_condition_dropdown"
								>
									{compareOperators}
								</SelectControl>
								<div>{FilterValueField}</div>
								{mergeTagsIcon}
								{resetCustomValue}
								<span
									onClick={() =>
										props.viewSettingsProps.addConditionalLogicRow(
											'viewSettings',
											'filters',
											groupIndex
										)
									}
									className="components-button is-primary add-new-and-cond"
								>
									AND
								</span>
								<i
									onClick={( event ) =>
										props.viewSettingsProps.removeConditionalLogicRow(
											'viewSettings',
											'filters',
											groupIndex,
											rowIndex
										)
									}
									className="far fa-trash-alt"
								></i>
							</div>
						);
					} );
				}

				return (
					<>
						{' '}
						{groupIndex !== 0 && (
							<div className="or-group-sep">
								<span>OR</span>
							</div>
						)}
						<div className="flyout_fields_cont filter-group-rows">
							{filterRows}
						</div>
					</>
				);
			}
		);
	}

	return (
		<>
			<div className="flyout_heading">
				<div>Add conditions to filter entries in view</div>
				<i
					onClick={() => {
						props.setOpenFlyout( false );
					}}
					className="far fa-times-circle"
				></i>
			</div>
			{filterConditionsGroups}
			{isVisible && (
				<Popover
					onFocusOutside={() => setIsVisible( false )}
					anchor={popoverAnchor}
				>
					<MergeTagList
						addMergeTagValue={addMergeTagValue}
						rowIndex={conditionRowIndex}
						groupIndex={conditionGroupIndex}
						viewSettingsProps={props.viewSettingsProps}
					/>
				</Popover>
			)}

			{isPro && (
				<Button
					className="filter-new-group-btn"
					onClick={() =>
						props.viewSettingsProps.addConditionalLogicGroup(
							'viewSettings',
							'filters'
						)
					}
					variant="primary"
				>
					Add New OR Group
				</Button>
			)}

			{!isPro && (
				<div className="filter-upgrade-msg">
					<div className="upgrade-text">
						<p>
							Filter entries by field value, entry date, date
							range etc. Add multiple filter conditions.
						</p>
						<p>
							Filter entries option is available in Personal
							License and above.
						</p>
					</div>
					<div className="ftr">
						<a href={upgradeLink} target="_blank">
							Upgrade License
						</a>
					</div>
				</div>
			)}
		</>
	);
};

export default FilterFlyout;
