import { PanelRow, TextareaControl } from '@wordpress/components';

const Gmaps = ( props ) => {
	const fieldProps = props.fieldProps;
	const viewFieldId = props.fieldProps.viewFieldId;

	return (
		<PanelRow>
			<TextareaControl
				help={
					<div>
						You can use text, html and WPForms Smart Tags to display
						custom data e.g { "{field_id = '3'}" }.
						<a
							href="https://formviewswp.com/docs/how-to-use-wpforms-smart-tags-in-custom-html-field/"
							target="_blank"
						>
							Click to know more
						</a>
					</div>
				}
				label="InfoWindow Content"
				value={
					!! fieldProps.fieldSettings.infoWindowContent
						? fieldProps.fieldSettings.infoWindowContent
						: ''
				}
				rows="18"
				onChange={ ( value ) => {
					props.fieldProps.updateFieldSetting(
						value,
						viewFieldId,
						'infoWindowContent'
					);
				} }
			/>
		</PanelRow>
	);
};

export default Gmaps;
