import { PanelRow, TextareaControl } from '@wordpress/components';

const CustomHtml = ( props ) => {
	const fieldProps = props.fieldProps;
	const viewFieldId = props.fieldProps.viewFieldId;
	const { _view_config } = window;
	const view_config = JSON.parse( _view_config );

	return (
		<PanelRow>
			<TextareaControl
				help={
					view_config.prefix === 'wpforms' && (
						<span>
							You can use WPForms Smart Tags to display custom
							data.
							<a
								href="https://formviewswp.com/docs/how-to-use-wpforms-smart-tags-in-custom-html-field/"
								target="_blank"
							>
								Click to know more
							</a>
						</span>
					)
				}
				label="Custom HTML"
				value={
					!! fieldProps.fieldSettings.html
						? fieldProps.fieldSettings.html
						: ''
				}
				rows="18"
				onChange={ ( value ) => {
					props.fieldProps.updateFieldSetting(
						value,
						viewFieldId,
						'html'
					);
				} }
			/>
		</PanelRow>
	);
};

export default CustomHtml;
