import SearchFlyout from './SearchFlyout';
import FilterFlyout from './FilterFlyout';
import DisplayConditionsFlyout from './DisplayConditionsFlyout';
import './index.css';
const SettingsFlyout = ( props ) => {
	const flyoutType = props.flyoutType;

	return (
		<div className="views_flyout_cont is_open">
			<div className="views_flyout">
				{ flyoutType === 'searchSettings' && (
					<SearchFlyout
						fieldSettingsProps={ props.fieldSettingsProps }
						setOpenFlyout={ props.setOpenFlyout }
					/>
				) }
				{ flyoutType === 'filterSettings' && (
					<FilterFlyout
						viewSettingsProps={ props.viewSettings }
						setOpenFlyout={ props.setOpenFlyout }
					/>
				) }
				{ flyoutType === 'displayConditions' && (
					<DisplayConditionsFlyout
						fieldSettingsProps={ props.fieldSettingsProps }
						setOpenFlyout={ props.setOpenFlyout }
					/>
				) }
			</div>
		</div>
	);
};
export default SettingsFlyout;
