import {
	PanelRow,
	TextControl,
	ToggleControl,
	TextareaControl,
} from '@wordpress/components';

const Email = ( props ) => {
	const fieldSettings = props.fieldProps.fieldSettings;
	const viewFieldId = props.fieldProps.viewFieldId;
	return (
		<>
			<PanelRow>
				<ToggleControl
					label="Make Email Address Clickable?"
					disabled={ ! props.fieldProps.isPro }
					help="Clicking on link will create a new email."
					checked={ fieldSettings.emailClickable }
					onChange={ ( value ) => {
						props.fieldProps.updateFieldSetting(
							value,
							viewFieldId,
							'emailClickable'
						);
					} }
				/>
			</PanelRow>
			{ fieldSettings.emailClickable && (
				<>
					<PanelRow>
						<TextControl
							help="Default email subject line."
							label="Email Subject"
							value={ fieldSettings.emailSubject }
							onChange={ ( value ) => {
								props.fieldProps.updateFieldSetting(
									value,
									viewFieldId,
									'emailSubject'
								);
							} }
						/>
					</PanelRow>
					<PanelRow>
						<TextareaControl
							help="Default email body content."
							label="Email Body"
							value={ fieldSettings.emailBody }
							rows="18"
							onChange={ ( value ) => {
								props.fieldProps.updateFieldSetting(
									value,
									viewFieldId,
									'emailBody'
								);
							} }
						/>
					</PanelRow>
				</>
			) }
		</>
	);
};

export default Email;
