import React from 'react';
import { Draggable } from 'react-beautiful-dnd';
import { FieldIcons } from '../../../../FieldIcons';
import './index.css';

const Field = ( props ) => {
	const fieldProps = props.fieldProps;
	const fieldId = props.fieldId;

	const fieldSettings = props.allFields[ fieldId ].fieldSettings;
	let label = fieldSettings.useCustomLabel
		? fieldSettings.label
		: props.allFields[ fieldId ].label;
	if ( label === '' ) {
		// if custom label is empty then use the default field label
		label = (
			<div className="hidden-label">
				{props.allFields[ fieldId ].label}
			</div>
		);
	}
	let fieldIcon = 'fa-link';
	const formFieldId = props.allFields[ fieldId ].formFieldId;
	let fieldType = formFieldId;

	if ( typeof props.formFields[ formFieldId ] !== 'undefined' ) {
		fieldType = props.formFields[ formFieldId ].fieldType;
	}
	if ( typeof FieldIcons[ fieldType ] !== 'undefined' ) {
		fieldIcon = FieldIcons[ fieldType ];
	}

	return (
		<Draggable
			key={fieldId}
			type={props.sectionName}
			draggableId={fieldId}
			index={props.index}
		>
			{( provided, snapshot ) => {
				const draggableClasses = snapshot.isDragging
					? 'activeField'
					: '';
				return (
					<div
						className={`${draggableClasses} field field_row`}
						{...provided.draggableProps}
						ref={provided.innerRef}
					>
						<span
							title="Drag to reorder"
							className="field_drag_icon view-setting-icon"
							{...provided.dragHandleProps}
						>
							<i className="fas fa-arrows-alt"></i>
						</span>
						<span
							onClick={( event ) =>
								fieldProps.setSelectedFieldId( fieldId )
							}
							className="field_label_row"
						>
							<i className={`fas ${fieldIcon}`}></i>
							{label}
						</span>
						<span
							className="add-new-field-icon"
							title="Add a new Field"
							onClick={( event ) =>
								props.openModal(
									event,
									'addField',
									'Add Field',
									props.colId,
									props.sectionName,
									props.index
								)
							}
						>
							<i className="fas fa-plus"></i>
						</span>
						<span
							className="close"
							title="Remove Field"
							onClick={( event ) =>
								fieldProps.removeField(
									event,
									fieldId,
									props.colId
								)
							}
						>
							<i className="far fa-times-circle"></i>
						</span>
					</div>
				);
			}}
		</Draggable>
	);
};

export default Field;
