import { PanelRow, CheckboxControl, BaseControl } from '@wordpress/components';
import './index.css';
const DatatableButtons = ( props ) => {
	const fieldProps = props.fieldProps;
	const setButton = ( isChecked, btn ) => {
		let checkedBtns = [ 'copy', 'csv', 'excel', 'pdf', 'print' ];

		if (
			typeof fieldProps.fieldSettings.datatableButtons !== 'undefined'
		) {
			checkedBtns = fieldProps.fieldSettings.datatableButtons;
		}

		// If checkbox is checked
		if ( isChecked ) {
			checkedBtns.push( btn );
		} else {
			// If checkbox is unchecked
			checkedBtns = checkedBtns.filter( ( ele ) => ele !== btn );
		}

		fieldProps.updateFieldSetting(
			checkedBtns,
			fieldProps.viewFieldId,
			'datatableButtons'
		);
	};

	const buttons = [ 'copy', 'csv', 'excel', 'pdf', 'print' ].map( ( btn ) => {
		let checked = true;
		if ( typeof fieldProps.fieldSettings.datatableButtons != 'undefined' ) {
			checked = fieldProps.fieldSettings.datatableButtons.includes( btn );
		}
		return (
			<CheckboxControl
				key={ `btn-${ btn }` }
				label={ btn.toUpperCase() }
				checked={ checked }
				onChange={ ( isChecked ) => setButton( isChecked, btn ) }
			/>
		);
	} );
	return (
		<>
			<PanelRow>
				<BaseControl
					id="wp-components-base-control-2"
					label="Choose which buttons to display:"
				></BaseControl>
			</PanelRow>
			<PanelRow>
				<div className="datatable_btns_cont">{ buttons }</div>
			</PanelRow>
		</>
	);
};

export default DatatableButtons;
