import { Button, TextControl, SelectControl } from '@wordpress/components';
import { isEmpty, cloneDeep } from 'lodash';
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd';

import { DisabledSearchFields } from '../../../Constants';
import './index.css';

const SearchFlyout = ( props ) => {
	const fieldSettingsProps = cloneDeep( props.fieldSettingsProps );
	const fieldSettings =
		fieldSettingsProps.fields[ fieldSettingsProps.selectedFieldId ]
			.fieldSettings;

	const searchableFields = Object.keys(
		fieldSettingsProps.formFields
	).reduce( ( result, key ) => {
		if (
			! DisabledSearchFields.includes(
				fieldSettingsProps.formFields[ key ].fieldType
			)
		) {
			result.push( fieldSettingsProps.formFields[ key ] );
		}
		return result;
	}, [] );

	const formFields = fieldSettingsProps.formFields;

	let searchFields = [];
	if ( !! fieldSettings.search && !! fieldSettings.search.fields ) {
		searchFields = fieldSettings.search.fields;
	}

	const isAlreadyAdded = ( key ) => {
		return searchFields.some( function ( arrayItem ) {
			return key === arrayItem.fieldId;
		} );
	};

	const formFieldOptions = Object.keys( formFields ).reduce(
		( result, key ) => {
			if (
				! DisabledSearchFields.includes( formFields[ key ].fieldType )
			) {
				result.push(
					<option
						disabled={ isAlreadyAdded( key ) }
						key={ `frmfieldoption${ key }` }
						value={ formFields[ key ].id }
					>
						{ formFields[ key ].label }
					</option>
				);
			}
			return result;
		},
		[]
	);

	// Add All Fields to Form Fields option

	formFieldOptions.unshift(
		<option
			key={ `frmfieldoption-all_fields` }
			value="all_fields"
			disabled={ isAlreadyAdded( 'all_fields' ) }
		>
			All Fields
		</option>
	);

	const updateSearchSetting = ( value, fieldType, index ) => {
		const search = fieldSettings.search;
		search.fields[ index ][ fieldType ] = value;
		// fill the search label field with the field label
		if (
			fieldType === 'fieldId' &&
			typeof formFields[ value ] !== 'undefined'
		) {
			search.fields[ index ].label = formFields[ value ].label;
		}

		fieldSettingsProps.updateFieldSetting(
			search,
			fieldSettingsProps.selectedFieldId,
			'search'
		);
	};

	const addSearchRow = ( e ) => {
		let search = {};
		if ( ! fieldSettings.search || ! fieldSettings.search.fields ) {
			search.fields = [];
		} else {
			search = fieldSettings.search;
		}

		// Loop through all form fields and add the next non selected field as selected in drodpown
		for ( var i = 0; i < searchableFields.length; i++ ) {
			const found = search.fields.find(
				( el ) => el.fieldId === searchableFields[ i ][ 'id' ]
			);
			if ( ! found ) {
				search.fields.push( {
					fieldId: searchableFields[ i ][ 'id' ],
					label: searchableFields[ i ][ 'label' ],
					inputType: '',
				} );
				break;
			}
		}
		fieldSettingsProps.updateFieldSetting(
			search,
			fieldSettingsProps.selectedFieldId,
			'search'
		);
	};

	const removeSearchRow = ( index ) => {
		const search = fieldSettings.search;
		search.fields.splice( index, 1 );
		fieldSettings.search = search;
		fieldSettingsProps.updateFieldSetting(
			search,
			fieldSettingsProps.selectedFieldId,
			'search'
		);
	};

	let searchRows = '';
	if ( !! fieldSettings.search && !! fieldSettings.search.fields ) {
		searchRows = fieldSettings.search.fields.map( ( field, index ) => {
			let searchInputTypes = [];
			let searchInputTypesOptions = [];
			let disabledState = true;
			if ( typeof formFields[ field.fieldId ] !== 'undefined' ) {
				if (
					field.fieldId === 'entryDate' ||
					formFields[ field.fieldId ][ 'fieldType' ] ===
						'date-time' ||
					formFields[ field.fieldId ][ 'fieldType' ] === 'date'
				) {
					disabledState = false;
					searchInputTypesOptions.push(
						<option key={ 'optdate' } value="date">
							Date
						</option>
					);
					searchInputTypesOptions.push(
						<option key={ 'optdaterange' } value="dateRange">
							Date Range
						</option>
					);
				} else if (
					formFields[ field.fieldId ][ 'fieldType' ] ===
						'listmultiselect' ||
					formFields[ field.fieldId ][ 'fieldType' ] ===
						'listcheckbox'
				) {
					disabledState = false;
					searchInputTypesOptions.push(
						<option key={ 'optdateselect' } value="select">
							Select
						</option>
					);
					searchInputTypesOptions.push(
						<option
							key={ 'optdatemultiselect' }
							value="multiselect"
						>
							MultiSelect
						</option>
					);
				} else {
					const nonTextFieldTypes = [
						'select',
						'checkbox',
						'radio',
						'listcheckbox',
						'listradio',
						'listmultiselect',
						'listselect',
						'listcountry',
						'liststate',
						'approvalStatus',
					];
					const fieldType =
						formFields[ field.fieldId ][ 'fieldType' ];
					if ( nonTextFieldTypes.includes( fieldType ) ) {
						searchInputTypesOptions = (
							<option
								key={ 'optdate${fieldType}' }
								value={ fieldType }
							>
								{ fieldType }
							</option>
						);
					} else {
						searchInputTypesOptions = (
							<option key={ 'optdatetext' } value="text">
								Text
							</option>
						);
					}
				}

				searchInputTypes = (
					<SelectControl
						key={ `inputypetext${ index }` }
						label=""
						disabled={ disabledState }
						value={ field.inputType }
						onChange={ ( value ) =>
							updateSearchSetting( value, 'inputType', index )
						}
					>
						{ searchInputTypesOptions }
					</SelectControl>
				);
			} else {
				searchInputTypes = (
					<SelectControl
						key="inputypetext"
						label=""
						value={ field.inputType }
						onChange={ ( value ) =>
							updateSearchSetting( value, 'inputType', index )
						}
					>
						<option value="text">Text</option>
					</SelectControl>
				);
			}

			return (
				<>
					<Draggable
						key={ `searchdragrow-${ field.fieldId }` }
						draggableId={ index + '-' + field.fieldId }
						index={ index }
					>
						{ ( provided, snapshot ) => {
							const draggableClasses = snapshot.isDragging
								? 'activeField searchActiveDragRow'
								: '';
							return (
								<div
									key={ `searchwid${ index }` }
									className={ `${ draggableClasses }  condition_field_row` }
									{ ...provided.draggableProps }
									ref={ provided.innerRef }
								>
									{ ' ' }
									<span
										className="row_drag_handle"
										{ ...provided.dragHandleProps }
									>
										<i className="fas fa-arrows-alt"></i>
									</span>
									<div className="search_field_dropdown">
										<SelectControl
											label=""
											value={ field.fieldId }
											onChange={ ( value ) =>
												updateSearchSetting(
													value,
													'fieldId',
													index
												)
											}
										>
											{ formFieldOptions }
										</SelectControl>
									</div>
									<div className="search_field_label">
										<TextControl
											label=""
											value={ field.label }
											onChange={ ( value ) => {
												updateSearchSetting(
													value,
													'label',
													index
												);
											} }
										/>
									</div>
									<div className="search_field_type">
										{ searchInputTypes }

										<i
											onClick={ () =>
												removeSearchRow( index )
											}
											className="fas fa-minus-circle"
										></i>
									</div>
									{ provided.placeholder }
								</div>
							);
						} }
					</Draggable>
				</>
			);
		} );
	}

	const onDragEnd = ( result ) => {
		const { destination, source } = result;

		if ( ! destination ) {
			return;
		}

		if (
			source.droppableId === destination.droppableId &&
			source.index === destination.index
		) {
			return;
		}
		const search = fieldSettings.search;
		const searchRows = search.fields;
		const draggedRowArr = searchRows.splice( source.index, 1 );
		searchRows.splice( destination.index, 0, draggedRowArr[ 0 ] );
		search.fields = searchRows;
		fieldSettingsProps.updateFieldSetting(
			search,
			fieldSettingsProps.selectedFieldId,
			'search'
		);
	};

	return (
		<>
			<div className="flyout_heading">
				<p>Add fields to display in Search Form</p>
				<i
					onClick={ () => {
						props.setOpenFlyout( false );
					} }
					className="far fa-times-circle"
				></i>
			</div>
			<div className="search_fields_cont_header">
				<div>Form Field</div>
				<div>Label</div>
				<div>Field Type</div>
			</div>

			{ !! fieldSettings.search && !! fieldSettings.search.fields && (
				<DragDropContext onDragEnd={ onDragEnd }>
					<Droppable droppableId="searchfieldsdroppable">
						{ ( provided, snapshot ) => {
							const droppableClasses = snapshot.isDraggingOver
								? 'activeRow'
								: '';
							return (
								<div
									className={
										droppableClasses +
										' droppableRow flyout_fields_cont'
									}
									{ ...provided.droppableProps }
									ref={ provided.innerRef }
								>
									{ searchRows }
									{ provided.placeholder }
								</div>
							);
						} }
					</Droppable>
				</DragDropContext>
			) }

			<Button onClick={ () => addSearchRow() } variant="primary">
				Add Search Field
			</Button>
		</>
	);
};
export default SearchFlyout;
