import { PanelRow, TextControl, RadioControl } from '@wordpress/components';
import './index.css';
const FileUpload = ( props ) => {
	const fieldSettings = props.fieldProps.fieldSettings;
	const viewFieldId = props.fieldProps.viewFieldId;
	return (
		<>
			<PanelRow>
				<RadioControl
					label="Display File as:"
					help=""
					selected={
						! fieldSettings.displayFileType
							? 'Link'
							: fieldSettings.displayFileType
					}
					options={ [
						{ label: 'Link', value: 'Link' },
						{ label: 'Image', value: 'Image' },
					] }
					onChange={ ( value ) => {
						props.fieldProps.updateFieldSetting(
							value,
							viewFieldId,
							'displayFileType'
						);
					} }
				/>
			</PanelRow>
			{ /* Link Settings */ }
			{ fieldSettings.displayFileType &&
				fieldSettings.displayFileType === 'Link' && (
					<PanelRow>
						<TextControl
							help="Link text to display in view"
							label="Link Text"
							value={
								!! fieldSettings.linkText
									? fieldSettings.linkText
									: ''
							}
							onChange={ ( value ) => {
								props.fieldProps.updateFieldSetting(
									value,
									viewFieldId,
									'linkText'
								);
							} }
						/>
					</PanelRow>
				) }
			{ /* Display as Image Settings */ }
			{ fieldSettings.displayFileType &&
				fieldSettings.displayFileType === 'Image' &&
				! fieldSettings.linkToSingleEntry && (
					<PanelRow>
						<RadioControl
							label="On Click:"
							help=""
							selected={
								!! fieldSettings.onClickAction
									? fieldSettings.onClickAction
									: 'doNothing'
							}
							options={ [
								{ label: 'Do Nothing', value: 'doNothing' },
								{ label: 'Open in new Tab', value: 'newTab' },
							] }
							onChange={ ( value ) => {
								props.fieldProps.updateFieldSetting(
									value,
									viewFieldId,
									'onClickAction'
								);
							} }
						/>
					</PanelRow>
				) }
			{ fieldSettings.displayFileType &&
				fieldSettings.displayFileType === 'Image' && (
					<PanelRow>
						<TextControl
							help="Image width in px, % or em. Default is 100%"
							label="Image Width"
							placeholder="100%"
							className="image-width"
							value={
								!! fieldSettings.imageWidth
									? fieldSettings.imageWidth
									: ''
							}
							onChange={ ( value ) => {
								props.fieldProps.updateFieldSetting(
									value,
									viewFieldId,
									'imageWidth'
								);
							} }
						/>
					</PanelRow>
				) }
		</>
	);
};

export default FileUpload;
