import {
	PanelRow,
	TextControl,
	ToggleControl,
	Tip,
	Button,
} from '@wordpress/components';
import {
	InlineEditFields,
	DisabledFilterSortFields,
	upgradeLink,
} from '../../../Constants';
import CustomHtml from './CustomHtml';
import Gmaps from './Gmaps';
import EditEntry from './EditEntry';
import DeleteEntry from './DeleteEntry';
import Email from './Email';
import FileUpload from './FileUpload';
import Search from './Search';
import SingleEntryLink from './SingleEntryLink';
import PrintSingleEntry from './PrintSingleEntry';
import EntryResumeLink from './EntryResumeLink';
import InlineEdit from './InlineEdit';
import DatatableButtons from './DatatableButtons';
import Calculations from './Calculations';

const Settings = ( props ) => {
	const viewFieldId = props.fieldProps.viewFieldId;
	const fieldSettings = props.fieldProps.fieldSettings;
	const fieldType = props.fieldProps.fieldType;

	const fieldProps = {
		fieldSettings,
		viewFieldId,
		isPro: props.fieldProps.isPro,
		updateFieldSetting: props.fieldProps.updateFieldSetting,
		openFlyoutHandler: props.fieldProps.openFlyoutHandler,
	};

	const { _view_config } = window;
	const view_config = JSON.parse( _view_config );

	return (
		<>
			{fieldType !== 'printSingleEntry' && (
				<PanelRow>
					<ToggleControl
						label="Use Custom Label?"
						checked={fieldSettings.useCustomLabel}
						onChange={( value ) => {
							props.fieldProps.updateFieldSetting(
								value,
								viewFieldId,
								'useCustomLabel'
							);
						}}
					/>
				</PanelRow>
			)}
			{fieldSettings.useCustomLabel && (
				<PanelRow>
					<TextControl
						help="Enter the custom Label. Keep it empty if you want to hide the label"
						placeholder="Label Hidden"
						label="Custom Label"
						value={fieldSettings.label}
						onChange={( value ) => {
							props.fieldProps.updateFieldSetting(
								value,
								viewFieldId,
								'label'
							);
						}}
					/>
				</PanelRow>
			)}
			{props.fieldProps.viewType === 'table' &&
				!DisabledFilterSortFields.includes( fieldType ) && (
					<PanelRow>
						<ToggleControl
							disabled={!props.fieldProps.isPro}
							label="Make this field Sortable?"
							checked={!!fieldSettings.sortableField}
							onChange={( value ) => {
								props.fieldProps.updateFieldSetting(
									value,
									viewFieldId,
									'sortableField'
								);
							}}
						/>
					</PanelRow>
				)}

			{ /* Display Link to Single Entry Checkbox */}
			{props.fieldProps.activeTab === 'multipleentries' &&
				( view_config.addons.includes( 'views_single_entry' ) ||
					!props.fieldProps.isPro ) &&
				[ 'name', 'text', 'file-upload', 'number', 'hidden' ].includes(
					fieldType
				) && (
					<PanelRow>
						<ToggleControl
							disabled={!props.fieldProps.isPro}
							label="Link to Single Entry"
							checked={!!fieldSettings.linkToSingleEntry}
							onChange={( value ) => {
								props.fieldProps.updateFieldSetting(
									value,
									viewFieldId,
									'linkToSingleEntry'
								);
							}}
						/>
					</PanelRow>
				)}

			{fieldType === 'html' && <CustomHtml fieldProps={fieldProps} />}
			{fieldType === 'gmaps' && <Gmaps fieldProps={fieldProps} />}
			{( fieldType === 'file_upload' ||
				fieldType === 'file-upload' ) && (
					<FileUpload fieldProps={fieldProps} />
				)}
			{fieldType === 'email' && <Email fieldProps={fieldProps} />}
			{fieldType === 'search' && <Search fieldProps={fieldProps} />}
			{fieldType === 'editEntry' && (
				<EditEntry fieldProps={fieldProps} />
			)}
			{fieldType === 'singleEntryLink' && (
				<SingleEntryLink fieldProps={fieldProps} />
			)}
			{fieldType === 'printSingleEntry' && (
				<PrintSingleEntry fieldProps={fieldProps} />
			)}

			{fieldType === 'entryResumeLink' && (
				<EntryResumeLink fieldProps={fieldProps} />
			)}
			{fieldType === 'deleteEntry' && (
				<DeleteEntry fieldProps={fieldProps} />
			)}
			{fieldType === 'datatableButtons' && (
				<DatatableButtons fieldProps={fieldProps} />
			)}
			{view_config.prefix == 'wpforms' &&
				!!props.fieldProps.viewSettings.inlineEdit &&
				!!props.fieldProps.viewSettings.inlineEdit.enable &&
				InlineEditFields.includes( fieldType ) && (
					<InlineEdit fieldProps={fieldProps} />
				)}

			{ /* Display Calculations Enable/Disable */}
			{( props.fieldProps.viewType === 'table' || props.fieldProps.viewType === 'datatable' ) &&
				( view_config.addons.includes( 'views_calculations' ) ||
					!props.fieldProps.isPro ) &&
				[ 'text', 'number', 'hidden' ].includes(
					fieldType
				) && (
					<Calculations fieldProps={fieldProps} />
				)}

			{!props.fieldProps.isPro &&
				!DisabledFilterSortFields.includes( fieldType ) && (
					<PanelRow>
						<Tip>
							<span className="upgrade-tip">
								Available in{' '}
								<a href={upgradeLink} target="_blank">
									pro version.
								</a>
							</span>
						</Tip>
					</PanelRow>
				)}
			{props.fieldProps.isPro &&
				![
					'gmaps',
					'pagination',
					'paginationInfo',
					'search',
					'entriesCount',
					'datatableButtons',
				].includes( fieldType ) && (
					<PanelRow>
						<ToggleControl
							label="Enable Display Conditions"
							checked={!!fieldSettings.displayConditionsToggle}
							onChange={( value ) => {
								props.fieldProps.updateFieldSetting(
									value,
									viewFieldId,
									'displayConditionsToggle'
								);
							}}
						/>
					</PanelRow>
				)}

			{props.fieldProps.isPro &&
				!!fieldSettings.displayConditionsToggle && (
					<PanelRow>
						<Button
							onClick={() =>
								props.fieldProps.openFlyoutHandler(
									'displayConditions'
								)
							}
							variant="secondary"
						>
							Edit Display Conditions
						</Button>
					</PanelRow>
				)}

			<PanelRow>
				<TextControl
					help="Separate multiple classes with spaces."
					label="ADDITIONAL CSS CLASS(ES)"
					value={fieldSettings.customClass}
					onChange={( value ) => {
						props.fieldProps.updateFieldSetting(
							value,
							viewFieldId,
							'customClass'
						);
					}}
				/>
			</PanelRow>
		</>
	);
};
export default Settings;
