import {
	PanelRow,
	Button,
	SelectControl,
	ToggleControl,
} from '@wordpress/components';
import { cloneDeep } from 'lodash';
const Search = ( props ) => {
	const fieldSettings = props.fieldProps.fieldSettings;
	const viewFieldId = props.fieldProps.viewFieldId;

	const updateSearchSetting = ( value, viewFieldId, field ) => {
		let search = {
			layout: 'horizontal',
		};
		if ( !! fieldSettings.search ) {
			search = cloneDeep( fieldSettings.search );
		}
		search[ field ] = value;

		props.fieldProps.updateFieldSetting( search, viewFieldId, 'search' );
	};

	return (
		<>
			<PanelRow>
				<SelectControl
					label="Search Layout"
					help="Select the fields layout for the search form."
					value={
						!! fieldSettings.search &&
						!! fieldSettings.search.layout
							? fieldSettings.search.layout
							: 'horizontal'
					}
					onChange={ ( value ) =>
						updateSearchSetting( value, viewFieldId, 'layout' )
					}
					options={ [
						{ label: 'Horizontal', value: 'horizontal' },
						{ label: 'Vertical', value: 'vertical' },
					] }
				/>
			</PanelRow>

			<PanelRow>
				<ToggleControl
					label="Display Clear button"
					help="Clicking on clear button will clear search form."
					checked={
						!! fieldSettings.search &&
						!! fieldSettings.search.clearButton
					}
					onChange={ ( value ) => {
						updateSearchSetting(
							value,
							viewFieldId,
							'clearButton'
						);
					} }
				/>
			</PanelRow>

			<PanelRow>
				<Button
					onClick={ () =>
						props.fieldProps.openFlyoutHandler( 'searchSettings' )
					}
					variant="primary"
				>
					Edit Search Form Fields
				</Button>
			</PanelRow>
		</>
	);
};

export default Search;
