import { TabPanel } from '@wordpress/components';
import { FieldIcons } from '../../FieldIcons';
import { proFieldIds, upgradeLink } from '../../Constants.js';
import './index.css';

const FieldsModal = ( props ) => {
	const { _view_config } = window;
	const view_config = JSON.parse( _view_config );
	const modalProps = props.modalProps;
	const disallowedField = [
		'save',
		'password',
		'pagebreak',
		'divider',
		'hr',
		'creditcard',
		'page-break',
		'password',
		'layout',
		'content',
		'allFields',
		'editEntry',
		'singleEntryLink',
		'deleteEntry',
		'submit',
		'approveEntries',
		'approvalStatus',
		'entryDate',
		'entryStatus',
		'entryLog',
		'sequenceNumber',
		'entryId',
		'entryResumeLink',
		'printSingleEntry',
		'entryAuthor',

	];
	const fields = Object.keys( modalProps.formFields ).reduce(
		( result, fieldId ) => {
			let fieldType = fieldId;
			if (
				typeof modalProps.formFields[ fieldId ].fieldType !==
				'undefined'
			) {
				fieldType = modalProps.formFields[ fieldId ].fieldType;
			}

			if ( !disallowedField.includes( fieldType ) ) {
				if ( fieldType !== 'allFields' ) {
					let fieldIcon = 'fa-link';

					if ( typeof FieldIcons[ fieldType ] !== 'undefined' ) {
						fieldIcon = FieldIcons[ fieldType ];
					} else if ( typeof FieldIcons[ fieldId ] !== 'undefined' ) {
						fieldIcon = FieldIcons[ fieldId ];
					}
					// Pro fields
					let proUpsellLink = '';

					if (
						proFieldIds.includes( fieldType ) &&
						!modalProps.isPro
					) {
						proUpsellLink = (
							<a
								className="pro-upsell-link"
								target="_blank"
								href={upgradeLink}
							>
								PRO
								<i className="fas fa-external-link-alt"></i>
							</a>
						);
					}
					result.push(
						<li
							className={
								proFieldIds.includes( fieldType ) &&
									!modalProps.isPro
									? 'profield-cont'
									: ''
							}
							key={`add${fieldId}`}
							onClick={( event ) =>
								modalProps.addField( event, fieldId, modalProps.position )
							}
						>
							<i className={`fas ${fieldIcon}`}></i>
							<span>
								{modalProps.formFields[ fieldId ].label}
							</span>
							{proUpsellLink}
						</li>
					);
				}
			}
			return result;
		},
		[]
	);

	const entryMetaFieldIds = [
		'sequenceNumber',
		'entryId',
		'entryDate',
		'editEntry',
		'singleEntryLink',
		'deleteEntry',
		'approvalStatus',
		'approveEntries',
		'entryAuthor',
	];
	if ( view_config.prefix === 'wpforms' ) {
		entryMetaFieldIds.push( 'entryStatus' );
		entryMetaFieldIds.push( 'entryResumeLink' );
		entryMetaFieldIds.push( 'entryLog' );
	}

	if (
		modalProps.activeSection === 'singleloop' &&
		view_config.prefix === 'wpforms'
	) {
		entryMetaFieldIds.push( 'printSingleEntry' );
	}

	// Add entry meta fields for loop sections
	const entryMetaFields = Object.keys( modalProps.formFields ).reduce(
		( result, fieldId ) => {
			let fieldType = fieldId;
			if (
				typeof modalProps.formFields[ fieldId ].fieldType !==
				'undefined'
			) {
				fieldType = modalProps.formFields[ fieldId ].fieldType;
			}
			if ( entryMetaFieldIds.includes( fieldType ) ) {
				let fieldIcon = 'fa-link';

				if ( typeof FieldIcons[ fieldType ] !== 'undefined' ) {
					fieldIcon = FieldIcons[ fieldType ];
				} else if ( typeof FieldIcons[ fieldId ] !== 'undefined' ) {
					fieldIcon = FieldIcons[ fieldId ];
				}

				// Pro fields
				let proUpsellLink = '';

				if ( proFieldIds.includes( fieldType ) && !modalProps.isPro ) {
					proUpsellLink = (
						<a
							className="pro-upsell-link"
							target="_blank"
							href={upgradeLink}
						>
							PRO
							<i className="fas fa-external-link-alt"></i>
						</a>
					);
				}

				result.push(
					<li
						key={`add${fieldId}`}
						onClick={( event ) =>
							modalProps.addField( event, fieldId, modalProps.position )
						}
					>
						<i className={`fas ${fieldIcon}`}></i>
						<span>{modalProps.formFields[ fieldId ].label}</span>
						{proUpsellLink}
					</li>
				);
			}
			return result;
		},
		[]
	);

	// Add All Fields Button
	if (
		modalProps.activeSection === 'loop' ||
		modalProps.activeSection === 'singleloop'
	) {
		fields.push(
			<li
				key={`addallFields`}
				onClick={( event ) => modalProps.addAllFields( event )}
			>
				<i className={`fas fa-plus`}></i>
				<span>Add All Form Fields</span>
			</li>
		);
	}

	return (
		<>
			{ /* For Widgets */}
			{( modalProps.activeSection === 'beforeloop' ||
				modalProps.activeSection === 'afterloop' ) && (
					<ul className="add-fields-list">{fields}</ul>
				)}

			{ /* For Form Fields */}
			{( modalProps.activeSection === 'loop' ||
				modalProps.activeSection === 'singleloop' ) && (
					<TabPanel
						className="fields_tabs_cont"
						activeClass="active-tab"
						orientation="vertical"
						tabs={[
							{
								name: 'form_fields',
								title: 'Form Fields',
								className: 'form_fields_tab',
							},
							{
								name: 'entry_meta',
								title: 'Other Fields',
								className: 'entry_meta_tab',
							},
						]}
					>
						{( tab ) => {
							if ( tab.name === 'form_fields' ) {
								return (
									<ul className="add-fields-list">{fields}</ul>
								);
							} else {
								return (
									<ul className="add-fields-list">
										{entryMetaFields}
									</ul>
								);
							}
						}}
					</TabPanel>
				)}
		</>
	);
};

export default FieldsModal;
