import { PanelRow, TextControl } from '@wordpress/components';

const DeleteEntry = (props) => {
	const fieldSettings = props.fieldProps.fieldSettings;
	const viewFieldId = props.fieldProps.viewFieldId;

	let linkText = '';
	if (typeof fieldSettings.linkText === 'undefined') {
		linkText = fieldSettings.label;
	} else {
		linkText = fieldSettings.linkText;
	}
	return (
		<PanelRow>
			<TextControl
				help="Enter the text you want to display for delete entry link."
				label="Link Text"
				value={linkText}
				onChange={(value) => {
					props.fieldProps.updateFieldSetting(
						value,
						viewFieldId,
						'linkText'
					);
				}}
			/>
		</PanelRow>
	);
};

export default DeleteEntry;
