import {
	PanelRow,
	SelectControl,
	TextControl,
	ToggleControl,
	Tip,
} from '@wordpress/components';
import { upgradeLink } from '../../../Constants.js';
const InlineEditSettings = ( props ) => {
	const viewSettingsProps = props.viewSettingsProps;
	const viewSettings = viewSettingsProps.viewSettings;

	const isPro = viewSettingsProps.isPro;
	let disabled = false;
	if ( ! isPro ) {
		disabled = true;
	}
	return (
		<>
			<PanelRow>
				<ToggleControl
					label="Enable Inline Edit"
					checked={
						!! viewSettings.inlineEdit &&
						( viewSettings.inlineEdit.enable
							? viewSettings.inlineEdit.enable
							: false )
					}
					disabled={ disabled }
					onChange={ ( value ) =>
						viewSettingsProps.saveViewSetting(
							value,
							'inlineEdit',
							'enable'
						)
					}
				/>
			</PanelRow>

			{ !! viewSettings.inlineEdit && viewSettings.inlineEdit.enable && (
				<>
					<PanelRow>
						<SelectControl
							label="Style"
							value={
								!! viewSettings.inlineEdit &&
								( viewSettings.inlineEdit.style
									? viewSettings.inlineEdit.style
									: 'jqueryui' )
							}
							onChange={ ( e ) =>
								viewSettingsProps.saveViewSetting(
									e,
									'inlineEdit',
									'style'
								)
							}
						>
							<option value="jqueryui">Jquery UI</option>
							<option value="plain">Plain</option>
							<option value="bootstrap3">Bootstrap 3</option>
						</SelectControl>
					</PanelRow>
					<PanelRow>
						<SelectControl
							label="Edit Mode"
							value={
								!! viewSettings.inlineEdit &&
								( viewSettings.inlineEdit.editMode
									? viewSettings.inlineEdit.editMode
									: 'popup' )
							}
							onChange={ ( value ) =>
								viewSettingsProps.saveViewSetting(
									value,
									'inlineEdit',
									'editMode'
								)
							}
						>
							<option value="popup">Popup</option>
							<option value="inline">Inline</option>
						</SelectControl>
					</PanelRow>
					<PanelRow>
						<TextControl
							label="Enable Edit Link Text"
							value={
								!! viewSettings.inlineEdit &&
								( viewSettings.inlineEdit.enableLabel
									? viewSettings.inlineEdit.enableLabel
									: 'Enable Inline Edit' )
							}
							onChange={ ( value ) =>
								viewSettingsProps.saveViewSetting(
									value,
									'inlineEdit',
									'enableLabel'
								)
							}
						/>
					</PanelRow>
					<PanelRow>
						<TextControl
							label="Disable Edit Link Text"
							value={
								!! viewSettings.inlineEdit &&
								( viewSettings.inlineEdit.disableLabel
									? viewSettings.inlineEdit.disableLabel
									: 'Disable Inline Edit' )
							}
							onChange={ ( value ) =>
								viewSettingsProps.saveViewSetting(
									value,
									'inlineEdit',
									'disableLabel'
								)
							}
						/>
					</PanelRow>
				</>
			) }
			{ ! isPro && (
				<PanelRow>
					<Tip>
						<span className="upgrade-tip">
							Inline Edit Entry Add-on is availabe in{ ' ' }
							<a href={ upgradeLink } target="_blank">
								Developer license.
							</a>
						</span>
					</Tip>
				</PanelRow>
			) }
		</>
	);
};

export default InlineEditSettings;
