import { PanelRow, SelectControl, Tip } from '@wordpress/components';
import { upgradeLink } from '../../../Constants.js';
const GeneralSettings = ( props ) => {
	const formList = props.viewSettingsProps.formsList;
	let viewSourceName = '';
	if ( !! props.viewSettingsProps.viewSource.formId ) {
		const selectedFormObj = formList.find(
			( o ) => o.id == props.viewSettingsProps.viewSource.formId
		);

		if ( !! selectedFormObj ) {
			viewSourceName = selectedFormObj.label;
		}
	}
	const viewSourceList = formList.map( ( form, index ) => {
		return (
			<option
				className="form_list_row"
				key={ `formList${ index }` }
				value={ form.id }
			>
				{ form.label }
			</option>
		);
	} );

	const ViewTypes = props.viewSettingsProps.viewTypes.map(
		( type, index ) => {
			let disabled = false;
			if ( ! props.viewSettingsProps.isPro && type !== 'table' ) {
				disabled = true;
			}
			return (
				<option
					className="form_list_row"
					key={ `viewType${ index }` }
					value={ type }
					disabled={ disabled }
				>
					{ type.charAt( 0 ).toUpperCase() + type.slice( 1 ) }
				</option>
			);
		}
	);

	let themes = {};
	if ( props.viewSettingsProps.viewType === 'table' ) {
		themes = {
			'pure-table-bordered': ' Table Bordered',
			'pure-table-horizontal': 'Table-Horizontal-Bordered',
			'pure-table-striped': 'Table-Striped',
			'pure-table-hover': ' Table-Hover',
			'rounded-table-bordered': 'Rounded Table-Horizontal-Bordered',
			'rounded-table-striped': 'Rounded Table-Striped',
			'rounded-table-hover': 'Rounded Table-Hover',
		};
	} else {
		themes = {
			default: 'Default 1 Column',
			'cards-2column': 'Cards - 2 Column',
			'cards-3column': 'Cards - 3 Column',
			'cards-4column': 'Cards - 4 Column',
		};
	}
	const themesOptions = Object.keys( themes ).map( ( value ) => {
		let disabled = false;
		if (
			! props.viewSettingsProps.isPro &&
			value !== 'pure-table-bordered'
		) {
			disabled = true;
		}

		return (
			<option
				value={ value }
				className="form_list_row"
				key={ `viewTheme${ value }` }
				disabled={ disabled }
			>
				{ themes[ value ] }
			</option>
		);
	} );

	return (
		<>
			<PanelRow>
				<SelectControl
					label="View Source"
					value={ props.viewSettingsProps.viewSource.formId }
					onChange={ ( value ) => {
						props.viewSettingsProps.updateViewSource( value );
					} }
				>
					{ viewSourceList }
				</SelectControl>
			</PanelRow>
			<PanelRow>
				<SelectControl
					label="View Type"
					onChange={ ( value ) => {
						props.viewSettingsProps.updateViewType( value );
					} }
					value={ props.viewSettingsProps.viewType }
				>
					{ ViewTypes }
				</SelectControl>
			</PanelRow>
			<PanelRow>
				<SelectControl
					label="View STYLING Theme"
					onChange={ ( value ) => {
						props.viewSettingsProps.updateViewTheme( value );
					} }
					value={
						!! props.viewSettingsProps.viewTheme
							? props.viewSettingsProps.viewTheme
							: 'pure-table-bordered'
					}
				>
					{ themesOptions }
				</SelectControl>
			</PanelRow>

			{ ! props.viewSettingsProps.isPro && (
				<PanelRow>
					<Tip>
						<span className="upgrade-tip">
							Display entries in List and DataTable format and
							more styling themes in{ ' ' }
							<a href={ upgradeLink } target="_blank">
								pro version.
							</a>
						</span>
					</Tip>
				</PanelRow>
			) }
		</>
	);
};

export default GeneralSettings;
