import React from 'react';
import {
	PanelRow,
	SelectControl,
	Tip,
	ToggleControl,
} from '@wordpress/components';
import { upgradeLink } from '../../../Constants.js';
const DataTablesSettings = ( props ) => {
	const viewSettingsProps = props.viewSettingsProps;
	const isPro = viewSettingsProps.isPro;
	if ( typeof viewSettingsProps.viewSettings.dataTable == 'undefined' ) {
		viewSettingsProps.viewSettings.dataTable = {
			responsive: false,
			pdfOrientation: 'portrait',
			pdfPageSize: 'A4',
		};
	}

	return (
		<>
			<PanelRow>
				<ToggleControl
					label="Make DataTable responsive"
					disabled={ ! isPro }
					checked={
						viewSettingsProps.viewSettings.dataTable.responsive ||
						false
					}
					onChange={ ( value ) =>
						viewSettingsProps.saveViewSetting(
							value,
							'dataTable',
							'responsive'
						)
					}
				/>
			</PanelRow>

			<PanelRow>
				<SelectControl
					label="PDF Orientation"
					disabled={ ! isPro }
					value={
						viewSettingsProps.viewSettings.dataTable
							.pdfOrientation || 'portrait'
					}
					onChange={ ( value ) =>
						viewSettingsProps.saveViewSetting(
							value,
							'dataTable',
							'pdfOrientation'
						)
					}
				>
					<option value="portrait">Portrait</option>
					<option value="landscape">Landscape</option>
				</SelectControl>
			</PanelRow>
			<PanelRow>
				<SelectControl
					disabled={ ! isPro }
					label="PDF Page Size"
					onChange={ ( event ) =>
						viewSettingsProps.saveViewSetting(
							event,
							'dataTable',
							'pdfPageSize'
						)
					}
					value={
						viewSettingsProps.viewSettings.dataTable.pdfPageSize ||
						'A4'
					}
				>
					<option value="A3">A3</option>
					<option value="A4">A4</option>
					<option value="A5">A5</option>
					<option value="LEGAL">LEGAL</option>
					<option value="LETTER">LETTER</option>
					<option value="TABLOID">TABLOID</option>
				</SelectControl>
			</PanelRow>
			{ ! isPro && (
				<PanelRow>
					<Tip>
						<span className="upgrade-tip">
							DataTable Add-on is availabe in{ ' ' }
							<a href={ upgradeLink } target="_blank">
								Developer license.
							</a>
						</span>
					</Tip>
				</PanelRow>
			) }
		</>
	);
};

export default DataTablesSettings;
