import { PanelRow, TextControl, ToggleControl } from '@wordpress/components';

const PrintSingleEntry = ( props ) => {
	const fieldSettings = props.fieldProps.fieldSettings;
	const viewFieldId = props.fieldProps.viewFieldId;

	let btnText = '';
	if ( typeof fieldSettings.btnText === 'undefined' ) {
		btnText = 'Print';
	} else {
		btnText = fieldSettings.btnText;
	}
	return (
		<>
			<PanelRow>
				<TextControl
					help="Enter the text you want to display for Print Button."
					label="Print Button Text"
					value={ btnText }
					onChange={ ( value ) => {
						props.fieldProps.updateFieldSetting(
							value,
							viewFieldId,
							'btnText'
						);
					} }
				/>
			</PanelRow>
		</>
	);
};

export default PrintSingleEntry;
