import React from 'react';
import { Panel, PanelBody, Notice } from '@wordpress/components';
import { FieldIcons } from '../../FieldIcons';
import Settings from './Settings';
const FieldSettings = ( props ) => {
	let settings = '';
	let fieldType = '';
	let formFieldId = '';
	let fieldSettings = '';
	let fieldIcon = 'fa-link';
	if ( !! props.fieldSettingsProps.selectedFieldId ) {
		const viewFieldId = props.fieldSettingsProps.selectedFieldId;
		formFieldId =
			props.fieldSettingsProps.fields[ viewFieldId ].formFieldId;
		fieldSettings =
			props.fieldSettingsProps.fields[ viewFieldId ].fieldSettings;

		// Set Field Type
		if ( !! props.fieldSettingsProps.formFields[ formFieldId ] ) {
			fieldType =
				props.fieldSettingsProps.formFields[ formFieldId ].fieldType;
		} else {
			fieldType = formFieldId;
		}
		// Set Field Icon

		if ( typeof FieldIcons[ fieldType ] !== 'undefined' ) {
			fieldIcon = FieldIcons[ fieldType ];
		}

		const fieldProps = {
			viewType: props.fieldSettingsProps.viewType,
			activeTab: props.fieldSettingsProps.activeTab,
			viewFieldId,
			fieldSettings,
			formFieldId,
			fieldType,
			isPro: props.fieldSettingsProps.isPro,
			updateFieldSetting: props.fieldSettingsProps.updateFieldSetting,
			viewSettings: props.fieldSettingsProps.viewSettings,
			openFlyoutHandler: props.openFlyoutHandler,
		};
		settings = <Settings fieldProps={ fieldProps } />;
	} else {
		settings = (
			<Notice isDismissible={ false }>
				Please click on field to view field settings.
			</Notice>
		);
	}

	return (
		<Panel>
			<React.Fragment key=".0">
				{ !! props.fieldSettingsProps.selectedFieldId && (
					<div className="field_info_header">
						<div className="sidebar_field_icon">
							<i className={ `fas ${ fieldIcon }` }></i>
						</div>
						<div className="sidebar_field_description">
							<div data-fieldid-label="ID: " data-fieldid="64">
								{ fieldSettings.label }
							</div>

							<div className="sidebar_field_info">
								<div className="sidebar_field_text">
									Type: { fieldType }
								</div>
								{ ! isNaN( formFieldId ) && (
									<div className="sidebar_field_text">
										ID: { formFieldId }
									</div>
								) }
							</div>
						</div>
					</div>
				) }
				<PanelBody key="fieldSettings" initialOpen={ true }>
					{ settings }
				</PanelBody>
			</React.Fragment>
		</Panel>
	);
};
export default FieldSettings;
