import React from 'react';
import { isEmpty } from 'lodash';
import { Panel, PanelBody } from '@wordpress/components';
import GeneralSettings from './GeneralSettings';
import MultipleEntriesSettings from './MultipleEntriesSettings';
import SingleEntrySettings from './SingleEntrySettings';
import FilterSettings from './FilterSettings';
import SortSettings from './SortSettings';
import EditEntrySettings from './EditEntrySettings';
import InlineEditSettings from './InlineEditSettings';
import DataTablesSettings from './DataTablesSettings';
import CalculationsSettings from './CalculationsSettings';
import './index.css';

const ViewSettings = ( props ) => {
	let addonsPanel = [];
	const viewSettingsProps = props.viewSettings;
	const { _view_config } = window;
	const view_config = JSON.parse( _view_config );
	const isPro = viewSettingsProps.isPro;
	// Add Datatable Settings
	if ( viewSettingsProps.viewType === 'datatable' || ! isPro ) {
		addonsPanel.push(
			<PanelBody
				key="datatableSettings"
				initialOpen={ false }
				title="DataTable Settings"
			>
				<DataTablesSettings viewSettingsProps={ viewSettingsProps } />
			</PanelBody>
		);
	}

	// check if Edit Entries addon is installed
	if (
		( typeof view_config.addons !== 'undefined' &&
			view_config.addons.includes( 'views_edit_entries' ) ) ||
		! isPro
	) {
		addonsPanel.push(
			<PanelBody
				key="editEntrySettings"
				initialOpen={ false }
				title="Edit Entries Settings"
			>
				<EditEntrySettings viewSettingsProps={ viewSettingsProps } />
			</PanelBody>
		);
	}

	// check if Inline edit entry addon is installed
	if (
		( typeof view_config.addons !== 'undefined' &&
			view_config.addons.includes( 'views_inline_edit' ) ) ||
		! isPro
	) {
		addonsPanel.push(
			<PanelBody
				key="inlineEditSettings"
				initialOpen={ false }
				title="Inline Edit Settings"
			>
				<InlineEditSettings viewSettingsProps={ viewSettingsProps } />
			</PanelBody>
		);
	}
	// check if single entry addon is installed
	if (
		( typeof view_config.addons !== 'undefined' &&
			view_config.addons.includes( 'views_single_entry' ) ) ||
		! isPro
	) {
		addonsPanel.push(
			<PanelBody
				key="singleEntrySettings"
				initialOpen={ false }
				title="Single Entry Settings"
			>
				<SingleEntrySettings viewSettingsProps={ viewSettingsProps } />
			</PanelBody>
		);
	}
	// check if single entry addon is installed
	if (
		typeof view_config.addons !== 'undefined' &&
		view_config.addons.includes( 'views_calculations2' )
	) {
		addonsPanel.push(
			<PanelBody
				key="calculationsSettings"
				initialOpen={ false }
				title="Calculations Settings"
			>
				<CalculationsSettings viewSettingsProps={ viewSettingsProps } />
			</PanelBody>
		);
	}

	return (
		<>
			<Panel>
				<React.Fragment key=".0">
					<PanelBody
						initialOpen={ false }
						title="View Source / Type / Theme"
					>
						<GeneralSettings
							viewSettingsProps={ viewSettingsProps }
						/>
					</PanelBody>

					<PanelBody
						initialOpen={ false }
						title="Multiple Entries Settings"
					>
						<MultipleEntriesSettings
							viewSettingsProps={ viewSettingsProps }
						/>
					</PanelBody>

					<PanelBody initialOpen={ false } title="Filter Settings">
						<FilterSettings
							viewSettingsProps={ viewSettingsProps }
							openFlyoutHandler={ props.openFlyoutHandler }
						/>
					</PanelBody>
					<PanelBody initialOpen={ false } title="Sort Settings">
						<SortSettings
							viewSettingsProps={ viewSettingsProps }
							openFlyoutHandler={ props.openFlyoutHandler }
						/>
					</PanelBody>
					{ addonsPanel }
				</React.Fragment>
			</Panel>
		</>
	);
};

export default ViewSettings;
