import { PanelRow, TextControl, ToggleControl, SelectControl } from '@wordpress/components';

const Calculations = ( props ) => {
	const fieldSettings = props.fieldProps.fieldSettings;
	const viewFieldId = props.fieldProps.viewFieldId;

	let linkText = '';
	if ( typeof fieldSettings.linkText === 'undefined' ) {
		linkText = fieldSettings.label;
	} else {
		linkText = fieldSettings.linkText;
	}
	return (
		<>
			<PanelRow>
				<ToggleControl
					disabled={!props.fieldProps.isPro}
					label="Enable Calculations"
					help="Display calculation result for this field in table footer."
					checked={!!fieldSettings.enableCalculations}
					onChange={( value ) => {
						props.fieldProps.updateFieldSetting(
							value,
							viewFieldId,
							'enableCalculations'
						);
					}}
				/>
			</PanelRow>
			{!!fieldSettings.enableCalculations && <PanelRow>
				<SelectControl
					label="Calculation Type"
					help="Select the calulcation type you want to display in footer."
					value={
						!!fieldSettings.calculationType
							? fieldSettings.calculationType
							: 'sum'
					}
					onChange={( value ) => {
						props.fieldProps.updateFieldSetting(
							value,
							viewFieldId,
							'calculationType'
						);
					}}
					options={[
						{ label: 'Sum', value: 'sum' },
						{ label: 'Average', value: 'average' },
					]}
				/>
			</PanelRow>}
			{!!fieldSettings.enableCalculations && <PanelRow>
				< TextControl
					help="Enter the text you want to display along with calculation. Use {result} for displaying calculation result."
					label="label"
					value={!!fieldSettings.calculationResultLabel
						? fieldSettings.calculationResultLabel
						: '{result}'}
					onChange={( value ) => {
						props.fieldProps.updateFieldSetting(
							value,
							viewFieldId,
							'calculationResultLabel'
						);
					}}
				/>

			</PanelRow>
			}
		</>
	);
};

export default Calculations;
