import {
	PanelRow,
	TextControl,
	ToggleControl,
	Tip,
} from '@wordpress/components';
import { upgradeLink } from '../../../Constants.js';
const SingleEntrySettings = ( props ) => {
	const viewSettingsProps = props.viewSettingsProps;
	const isPro = viewSettingsProps.isPro;
	const { _view_config } = window;
	const view_config = JSON.parse( _view_config );
	return (
		<div>
			<PanelRow>
				<TextControl
					help="Enter the text to display for Go back link on Single Entry
					Page"
					placeholder="Go back"
					label="Back Link Label"
					disabled={ ! isPro }
					value={
						typeof viewSettingsProps.viewSettings.singleEntry !=
						'undefined'
							? viewSettingsProps.viewSettings.singleEntry
									.backLinkLabel
							: ''
					}
					onChange={ ( value ) =>
						viewSettingsProps.saveViewSetting(
							value,
							'singleEntry',
							'backLinkLabel'
						)
					}
				/>
			</PanelRow>

			{ view_config.prefix === 'wpforms' && (
				<PanelRow>
					<ToggleControl
						disabled={ ! isPro }
						label="Mark Entry as Read/Viewed when Single Entry is
							Viewed"
						checked={
							!! viewSettingsProps.viewSettings.singleEntry
								.markViewed
						}
						onChange={ ( value ) =>
							viewSettingsProps.saveViewSetting(
								value,
								'singleEntry',
								'markViewed'
							)
						}
					/>
				</PanelRow>
			) }
			{ ! isPro && (
				<PanelRow>
					<Tip>
						<span className="upgrade-tip">
							Single Entry Add-on is availabe in{ ' ' }
							<a href={ upgradeLink } target="_blank">
								Developer license.
							</a>
						</span>
					</Tip>
				</PanelRow>
			) }
		</div>
	);
};
export default SingleEntrySettings;
