import React from 'react';
import Row from './Row';
import { Droppable } from 'react-beautiful-dnd';
import { Button } from '@wordpress/components';
import { upgradeLink } from '../Constants.js';
import './index.css';

const Section = ( props ) => {
	const { _view_config } = window;
	const view_config = JSON.parse( _view_config );
	const rowsArray = props.sectionRows;
	const rows = rowsArray.map( ( rowId, index ) => {
		return (
			<Row
				rowColumns={props.allRows[ rowId ].cols}
				sectionName={props.sectionName}
				key={rowId}
				rowId={rowId}
				allColumns={props.allColumns}
				formFields={props.formFields}
				allFields={props.allFields}
				viewType={props.viewType}
				index={index}
				// addFieldModal={props.addFieldModal}
				fieldProps={props.fieldProps}
				addRow={props.addRow}
				openModal={props.openModal}
				deleteRow={props.deleteRow}
			/>
		);
	} );

	return (
		<>
			{ /* if section is not single entry  OR  single entry tab with pro and single entry addon installed */}
			{( props.sectionName !== 'singleloop' ||
				( props.sectionName === 'singleloop' &&
					props.isPro &&
					view_config.addons.includes( 'views_single_entry' ) ) ) && (
					<div className="view-section-container viewTabChild">
						<div className="section-heading-cont">
							<h2 className="viewSecHeading"> {props.sectionLabel} </h2>
							{( props.sectionName == 'loop' || props.sectionName == 'singleloop' ) && <Button
								variant="secondary"
								onClick={() =>
									props.clearAllFields( props.sectionName )
								}
								text="Clear All Fields"
							></Button>


							}
						</div>
						<Droppable
							type={`section-${props.sectionName}`}
							droppableId={props.sectionName}
						>
							{( provided, snapshot ) => {
								const droppableClasses = snapshot.isDraggingOver
									? 'activeRow'
									: '';
								return (
									<div
										className={
											droppableClasses + ' droppableRow'
										}
										{...provided.droppableProps}
										ref={provided.innerRef}
									>
										{rows}
										{provided.placeholder}
									</div>
								);
							}}
						</Droppable>

						{( ( props.viewType == 'list' &&
							props.sectionName == 'loop' ) ||
							props.sectionName != 'loop' ) && (
								<span
									className="addRow"
									onClick={( event ) =>
										props.addRow( event, props.sectionName )
									}
								>
									<span className="addRowIcon">
										<i className="fas fa-plus-circle"></i>
									</span>{' '}
									<span className="addRowText">Add Row</span>
								</span>
							)}
					</div >
				)}
			{ /* if section is single entry  AND  single entry tab  and single entry addon  is not installed */}
			{
				props.sectionName === 'singleloop' &&
				!view_config.addons.includes( 'views_single_entry' ) && (
					<div className="view-section-container viewTabChild">
						<div className="single-entry-upgrade-msg">
							<p>
								Display Entry full details on a Single page. Add
								"More Details" link to entries list in multiple
								entries view.
							</p>
							<p>
								Single Entry Add-on is available in Developer
								License.
							</p>
							<div className="ftr">
								<a href={upgradeLink} target="_blank">
									Upgrade to Developer License
								</a>
							</div>
						</div>
					</div>
				)
			}
		</>
	);
};

export default Section;
