import { PanelRow, Button, SelectControl } from '@wordpress/components';
import { isEmpty } from 'lodash';
import { DisabledFilterSortFields } from '../../../Constants';
import './index.css';

const SortSettings = ( props ) => {
	const viewSettingsProps = props.viewSettingsProps;
	const formFields = viewSettingsProps.formFields;
	let sortRows = '';
	let sortSettings = [];
	/**
	 * Get Form Fields to show in dropdown
	 */
	const formFieldOptions = Object.keys( formFields ).reduce(
		( result, key ) => {
			if (
				! DisabledFilterSortFields.includes(
					formFields[ key ].fieldType
				)
			) {
				result.push(
					<option
						key={ `frmfieldoption${ key }` }
						value={ formFields[ key ].id }
					>
						{ formFields[ key ].label }
					</option>
				);
			}
			return result;
		},
		[]
	);

	// backward Compatibility
	if (
		typeof viewSettingsProps.viewSettings.sort.direction === 'undefined'
	) {
		sortSettings = viewSettingsProps.viewSettings.sort;
	}

	/**
	 * Show Add New Filter button if no filter values present
	 */
	if ( isEmpty( sortSettings ) ) {
		sortRows = '';
	} else {
		sortRows = sortSettings.map( ( sort, index ) => {
			return (
				<PanelRow
					key={ `sortRow${ index }` }
					className="sort_settings_cont"
				>
					<div className="condition_field_row">
						<SelectControl
							onChange={ ( value ) =>
								viewSettingsProps.saveViewSetting(
									value,
									'sort',
									'field',
									index
								)
							}
							value={ sort.field }
						>
							{ formFieldOptions }
						</SelectControl>
						<SelectControl
							onChange={ ( value ) =>
								viewSettingsProps.saveViewSetting(
									value,
									'sort',
									'value',
									index
								)
							}
							value={ sort.value }
						>
							<option value="ASC">ASC</option>
							<option value="DESC">DESC</option>
						</SelectControl>

						<i
							onClick={ ( event ) =>
								viewSettingsProps.removeConditionRow(
									event,
									'sort',
									index
								)
							}
							className="fas fa-minus-circle"
						></i>
					</div>
				</PanelRow>
			);
		} );
	}

	return (
		<>
			{ sortRows }
			<PanelRow className="sort_settings_cont">
				<Button
					onClick={ () =>
						props.viewSettingsProps.addConditionRow( 'sort' )
					}
					variant="secondary"
				>
					Add New Sort Condition
				</Button>
			</PanelRow>
		</>
	);
};

export default SortSettings;
