import React from 'react';
import { Droppable } from 'react-beautiful-dnd';
import Field from './Field';
import './index.css';

const Column = ( props ) => {
	const columnFields = props.columnFields.map( ( fieldId, index ) => {
		return (
			<Field
				openModal={props.openModal}
				formFields={props.formFields}
				sectionName={props.sectionName}
				allFields={props.allFields}
				key={index}
				index={index}
				colId={props.colId}
				fieldId={fieldId}
				fieldProps={props.fieldProps}
			/>
		);
	} );

	const addNewLabel =
		props.sectionName === 'beforeloop' || props.sectionName === 'afterloop'
			? 'Add Widget'
			: 'Add Field';
	return (
		<div className={'col-cont pure-u-1 pure-u-md-' + props.size}>
			<div className="column">
				<Droppable
					type={props.sectionName}
					droppableId={props.colId}
				>
					{( provided, snapshot ) => {
						const droppableClasses = snapshot.isDraggingOver
							? 'activeColumn'
							: '';
						return (
							<div
								className={
									droppableClasses + ' droppableColumn'
								}
								{...provided.droppableProps}
								ref={provided.innerRef}
							>


								{/* Fields within Section */}
								{columnFields}


								{provided.placeholder}
								<div
									className="view-empty-placeholder"
									onClick={( event ) =>
										props.openModal(
											event,
											'addField',
											addNewLabel,
											props.colId,
											props.sectionName
										)
									}
								>
									<i className="fas fa-plus-circle"></i>
									<span>{addNewLabel}</span>
								</div>
							</div>
						);
					}}
				</Droppable>
			</div>
		</div>
	);
};

export default Column;
