import React, { useState } from 'react';
import { Popover, Button, Icon, TextControl } from '@wordpress/components';
import { copy, shortcode } from '@wordpress/icons';
import { CopyToClipboard } from 'react-copy-to-clipboard';

import './index.css';
const Toolbar = ( props ) => {
	const [ isVisible, setIsVisible ] = useState( false );
	const toggleVisible = () => {
		setIsVisible( ( state ) => ! state );
		//setIsVisible( true );
	};
	const { _view_id, _view_config } = window;

	const view_config = JSON.parse( _view_config );
	let viewShortcode = '';
	if ( view_config.prefix === 'wpforms' ) {
		viewShortcode = `[wpforms-views id="${ _view_id }"]`;
	} else if ( view_config.prefix === 'nf' ) {
		viewShortcode = `[nf-views id="${ _view_id }"]`;
	} else {
		viewShortcode = `[cf7-views id="${ _view_id }"]`;
	}

	return (
		<>
			<div className="views_toolbar_settings">
				<h2 className="title-placeholder">Editing View:</h2>
				<TextControl
					className="view_title"
					value={ !! props.title ? props.title : '' }
					onChange={ ( value ) => props.updateViewTitle( value ) }
				/>
			</div>
			<div className="views_toolbar_buttons">
				<>
					<div className="view_embed_shortcode_cont">
						<Button
							icon={ shortcode }
							iconPosition="left"
							iconSize="20"
							variant="secondary"
							onClick={ toggleVisible }
							text="Embed"
						></Button>
						{ isVisible && (
							<Popover
								onFocusOutside={ () => setIsVisible( false ) }
							>
								<div className="view_embed_shortcode_popup">
									<p>
										Copy and paste the shortcode within your
										page editor.
									</p>
									<div className="view_embed_shortcode_content">
										<div className="view_embed_shortcode">
											{ viewShortcode }
										</div>
										<CopyToClipboard text={ viewShortcode }>
											<Button
												icon={ copy }
												iconPosition="left"
												variant="secondary"
												text="Copy"
											></Button>
										</CopyToClipboard>
									</div>
								</div>
							</Popover>
						) }
					</div>
					<Button
						icon={ <i className="fas fa-save"></i> }
						iconPosition="left"
						variant="primary"
						isBusy={ props.isSavingView }
						text={ props.isSavingView ? 'Saving..' : 'Save View' }
						onClick={ props.saveView }
						className="save_view_btn"
					></Button>
				</>
			</div>
		</>
	);
};
export default Toolbar;
