import {
	PanelRow,
	TextControl,
	ToggleControl,
	Tip,
} from '@wordpress/components';
import { upgradeLink } from '../../../Constants.js';
const EditEntrySettings = ( props ) => {
	const viewSettingsProps = props.viewSettingsProps;
	const { _view_config } = window;
	const view_config = JSON.parse( _view_config );

	const isPro = viewSettingsProps.isPro;
	let disabled = false;
	if ( ! isPro ) {
		disabled = true;
	}

	return (
		<>
			<PanelRow>
				<TextControl
					help="Enter the page link which contains your Form. Users will be
					redirected to this page to edit entry"
					label="Edit Page Link"
					value={
						typeof viewSettingsProps.viewSettings.editEntries !=
						'undefined'
							? viewSettingsProps.viewSettings.editEntries
									.editPage
							: ''
					}
					disabled={ disabled }
					onChange={ ( value ) =>
						viewSettingsProps.saveViewSetting(
							value,
							'editEntries',
							'editPage'
						)
					}
				/>
			</PanelRow>

			{ ( ( typeof view_config.addons !== 'undefined' &&
				view_config.addons.includes( 'views_approve_entries' ) ) ||
				! isPro ) && (
				<PanelRow>
					<ToggleControl
						label="Mark entries Unapproved after Edit"
						checked={
							typeof viewSettingsProps.viewSettings.editEntries !=
							'undefined'
								? viewSettingsProps.viewSettings.editEntries
										.unapproveAfterEdit
								: false
						}
						disabled={ disabled }
						onChange={ ( e ) =>
							viewSettingsProps.saveViewSetting(
								e,
								'editEntries',
								'unapproveAfterEdit'
							)
						}
					/>
				</PanelRow>
			) }
			{ ! isPro && (
				<PanelRow>
					<Tip>
						<span className="upgrade-tip">
							Edit Entry Add-on is availabe in{ ' ' }
							<a href={ upgradeLink } target="_blank">
								Developer license.
							</a>
						</span>
					</Tip>
				</PanelRow>
			) }
		</>
	);
};

export default EditEntrySettings;
