import React from 'react';
import {
	PanelRow,
	TextControl,
	ToggleControl,
	Tip,
} from '@wordpress/components';
import { upgradeLink } from '../../../Constants.js';
const MultipleEntriesSettings = ( props ) => {
	let approvedSubmissionsField = <div></div>;
	let partialEntriesField = <div></div>;
	let pluginSpeficSettings = '';
	let inlineEditSetting = '';
	let view_config = '';
	const viewSettingsProps = props.viewSettingsProps;
	const viewSettings = viewSettingsProps.viewSettings;
	const { _view_config } = window;
	if ( typeof _view_config !== 'undefined' ) {
		view_config = JSON.parse( _view_config );
	}

	const isPro = viewSettingsProps.isPro;
	let disabled = false;
	if ( ! isPro ) {
		disabled = true;
	}

	if (
		( typeof view_config.addons !== 'undefined' &&
			view_config.addons.includes( 'views_approved_submissions' ) ) ||
		! isPro
	) {
		approvedSubmissionsField = (
			<PanelRow>
				<ToggleControl
					label="Display only Approved Submissions"
					checked={
						!! viewSettings.multipleentries.approvedSubmissions
					}
					disabled={ disabled }
					onChange={ ( value ) =>
						viewSettingsProps.saveViewSetting(
							value,
							'multipleentries',
							'approvedSubmissions'
						)
					}
				/>
			</PanelRow>
		);
	}

	// Display Partial Entries
	if (
		( typeof view_config.addons !== 'undefined' &&
			view_config.addons.includes( 'wpforms-save-resume' ) ) ||
		! isPro
	) {
		partialEntriesField = (
			<PanelRow>
				<ToggleControl
					label="Display Partial Entries created by Save & Resume Add-on"
					checked={ !! viewSettings.multipleentries.partial }
					disabled={ disabled }
					onChange={ ( value ) =>
						viewSettingsProps.saveViewSetting(
							value,
							'multipleentries',
							'partial'
						)
					}
				/>
			</PanelRow>
		);
	}

	if ( view_config.prefix == 'wpforms' ) {
		pluginSpeficSettings = (
			<>
				<PanelRow>
					<ToggleControl
						label="Display only Starred Entries"
						checked={ !! viewSettings.multipleentries.starred }
						disabled={ disabled }
						onChange={ ( value ) =>
							viewSettingsProps.saveViewSetting(
								value,
								'multipleentries',
								'starred'
							)
						}
					/>
				</PanelRow>
				<PanelRow>
					<ToggleControl
						label="Display only Viewed Entries"
						checked={ !! viewSettings.multipleentries.viewed }
						disabled={ disabled }
						onChange={ ( value ) =>
							viewSettingsProps.saveViewSetting(
								value,
								'multipleentries',
								'viewed'
							)
						}
					/>
				</PanelRow>
			</>
		);
	}

	return (
		<>
			<PanelRow>
				<TextControl
					label="Entries Limit Per Page"
					value={ viewSettings.multipleentries.perPage }
					onChange={ ( value ) =>
						viewSettingsProps.saveViewSetting(
							value,
							'multipleentries',
							'perPage'
						)
					}
				/>
			</PanelRow>
			<PanelRow>
				<TextControl
					label='"No Result" Text'
					placeholder="No records found"
					value={ viewSettings.multipleentries.noRecordsText }
					onChange={ ( value ) =>
						viewSettingsProps.saveViewSetting(
							value,
							'multipleentries',
							'noRecordsText'
						)
					}
				/>
			</PanelRow>
			<PanelRow>
				<ToggleControl
					disabled={ disabled }
					checked={ viewSettings.multipleentries.loggedin }
					label="Display Only Logged in User Entries"
					onChange={ ( value ) =>
						viewSettingsProps.saveViewSetting(
							value,
							'multipleentries',
							'loggedin'
						)
					}
				/>
			</PanelRow>
			<PanelRow>
				<ToggleControl
					disabled={ disabled }
					label="Hide View Data until search is performed"
					checked={ !! viewSettings.multipleentries.hideDataOnLoad }
					onChange={ ( value ) =>
						viewSettingsProps.saveViewSetting(
							value,
							'multipleentries',
							'hideDataOnLoad'
						)
					}
				/>
			</PanelRow>
			<PanelRow>
				<ToggleControl
					disabled={ disabled }
					label="Hide Empty Fields"
					checked={ !! viewSettings.multipleentries.hideEmptyFields }
					onChange={ ( value ) =>
						viewSettingsProps.saveViewSetting(
							value,
							'multipleentries',
							'hideEmptyFields'
						)
					}
				/>
			</PanelRow>

			{ approvedSubmissionsField }
			{ partialEntriesField }
			{ pluginSpeficSettings }
			{ inlineEditSetting }
			{ ! isPro && (
				<PanelRow>
					<Tip>
						<span className="upgrade-tip">
							Get more options in{ ' ' }
							<a href={ upgradeLink } target="_blank">
								pro version.
							</a>
						</span>
					</Tip>
				</PanelRow>
			) }
		</>
	);
};

export default MultipleEntriesSettings;
