import React, { useEffect } from 'react';
import { isEmpty } from 'lodash';

import { DisabledFilterSortFields } from '../../../Constants';
import './index.css';

const CalculationsSettings = ( props ) => {
	useEffect( () => {
		//ReactTooltip.rebuild();
	} );

	const viewSettingsProps = props.viewSettingsProps;
	const formFields = viewSettingsProps.formFields;
	let calculationsRows = '';
	if (
		viewSettingsProps.viewSettings?.calculations?.calcRows === undefined ||
		isEmpty( viewSettingsProps.viewSettings.calculations.calcRows )
	) {
		calculationsRows = (
			<button
				onClick={ ( event ) =>
					viewSettingsProps.addConditionRow( event, 'calculations' )
				}
				className="pure-button button-primary button-large"
			>
				Add New
			</button>
		);
	} else {
		calculationsRows = Object.keys(
			viewSettingsProps.viewSettings.calculations.calcRows
		).map( ( key, index ) => {
			return (
				<div key={ `clacrow - ${ index }` } className="calcRow pure-g">
					<div className="pure-u-1-3">
						<span>
							<input
								value={
									viewSettingsProps.viewSettings.calculations
										.calcRows[ key ].calcName
								}
								type="text"
								className="calcName"
								onChange={ ( event ) =>
									viewSettingsProps.saveViewSetting(
										event,
										'calcRows',
										'calcName',
										index
									)
								}
							/>
						</span>
						<span>
							Decimals
							<input
								type="text"
								value={
									viewSettingsProps.viewSettings.calculations
										.calcRows[ key ].calcDecimals
								}
								className="calcDecimals"
								onChange={ ( event ) =>
									viewSettingsProps.saveViewSetting(
										event,
										'calcRows',
										'calcDecimals',
										index
									)
								}
							/>
						</span>
					</div>
					<div className="pure-u-2-3">
						<textarea
							rows="4"
							cols="30"
							value={
								viewSettingsProps.viewSettings.calculations
									.calcRows[ key ].calcEq
							}
							className="calcEq"
							onChange={ ( event ) =>
								viewSettingsProps.saveViewSetting(
									event,
									'calcRows',
									'calcEq',
									index
								)
							}
						/>
						<i
							data-tip={ index }
							data-event="click"
							data-for="calcMergeTags"
							className="fas fa-bars mergeTagsIcon"
						></i>
						<i
							onClick={ ( event ) =>
								viewSettingsProps.addConditionRow(
									event,
									'calculations'
								)
							}
							className="fas fa-plus-circle"
						></i>
						<i
							onClick={ ( event ) =>
								viewSettingsProps.removeConditionRow(
									event,
									'calculations',
									index
								)
							}
							className="fas fa-minus-circle"
						></i>
					</div>
				</div>
			);
		} );
	}
	return (
		<div className="views-calculations-settings">
			<div className="pure-control-group">
				<div className="pure-g views-calculations-settings-header">
					<div className="pure-u-1-3"> Calculation Name</div>
					<div className="pure-u-2-3">Equation</div>
				</div>
				{ calculationsRows }
			</div>

			{ /* <ReactTooltip
				clickable={ true }
				globalEventOff="click"
				className="mergeTagsToolTip"
				id="calcMergeTags"
				type="error"
				getContent={ ( index ) => (
					<ul className="calcMergeTagsCont">
						{ Object.keys( formFields ).reduce( ( result, key ) => {
							if (
								! DisabledFilterSortFields.includes(
									formFields[ key ].fieldType
								) &&
								formFields[ key ].fieldType !== 'entryId' &&
								formFields[ key ].fieldType !== 'entryDate'
							) {
								result.push(
									<li
										key={ `frmfieldsTags${ key }` }
										onClick={ ( e ) =>
											viewSettingsProps.addMergeTagToCalculationInput(
												e,
												index,
												`{field_id="${ formFields[ key ].id }"}`
											)
										}
									>
										<span>
											{ formFields[ key ].label }{ ' ' }
										</span>
									</li>
								);
							}
							return result;
						}, [] ) }
					</ul>
				) }
			/> */ }
		</div>
	);
};

export default CalculationsSettings;
