import { PanelRow, TextControl } from '@wordpress/components';

const EditEntry = (props) => {
	const fieldProps = props.fieldProps;
	let linkText = '';
	if (typeof fieldProps.fieldSettings.linkText === 'undefined') {
		linkText = fieldProps.fieldSettings.label;
	} else {
		linkText = fieldProps.fieldSettings.linkText;
	}
	return (
		<PanelRow>
			<TextControl
				help="Enter the text you want to display for edit entry link."
				label="Link Text"
				value={linkText}
				onChange={(value) => {
					fieldProps.updateFieldSetting(
						value,
						fieldProps.viewFieldId,
						'linkText'
					);
				}}
			/>
		</PanelRow>
	);
};

export default EditEntry;
