import { PanelRow, RadioControl } from '@wordpress/components';

const InlineEdit = (props) => {
	const fieldSettings = props.fieldProps.fieldSettings;
	const viewFieldId = props.fieldProps.viewFieldId;

	return (
		<PanelRow>
			<RadioControl
				label="Inline Edit"
				help=""
				selected={fieldSettings.inlineEdit}
				options={[
					{ label: 'Enabled', value: 'enabled' },
					{ label: 'Disabled', value: 'disabled' },
				]}
				onChange={(value) => {
					props.fieldProps.updateFieldSetting(
						value,
						viewFieldId,
						'inlineEdit'
					);
				}}
			/>
		</PanelRow>
	);
};

export default InlineEdit;
