import React from 'react';
import LayoutModal from './LayoutModal';
import FieldsModal from './FieldsModal';
import { Modal as WPModal } from '@wordpress/components';
import './index.css';

const Modal = ( props ) => {
	const modalProps = props.modalProps;
	let modal = '';

	// return;
	if ( modalProps.type === 'layout' ) {
		modal = <LayoutModal modalProps={ modalProps } />;
	} else if ( modalProps.type === 'addField' ) {
		modal = <FieldsModal modalProps={ modalProps } />;
	}

	return (
		<>
			{ modalProps.isVisible && (
				<WPModal
					title={ modalProps.label }
					onRequestClose={ modalProps.closeModal }
					className={
						modalProps.type === 'layout'
							? 'view_layout_modal'
							: 'view_field_modal'
					}
				>
					{ modal }
				</WPModal>
			) }
		</>
	);
};

export default Modal;
