import { PanelRow, TextControl, ToggleControl } from '@wordpress/components';

const EntryResumeLink = ( props ) => {
	const fieldSettings = props.fieldProps.fieldSettings;
	const viewFieldId = props.fieldProps.viewFieldId;

	let linkText = '';
	if ( typeof fieldSettings.linkText === 'undefined' ) {
		linkText = fieldSettings.label;
	} else {
		linkText = fieldSettings.linkText;
	}
	return (
		<>
			<PanelRow>
				<ToggleControl
					label="Open link in new Tab?"
					checked={ !! fieldSettings.newTab }
					onChange={ ( value ) => {
						props.fieldProps.updateFieldSetting(
							value,
							viewFieldId,
							'newTab'
						);
					} }
				/>
			</PanelRow>
			<PanelRow>
				<TextControl
					help="Enter the text you want to display for resume form submission link."
					label="Link Text"
					value={ linkText }
					onChange={ ( value ) => {
						props.fieldProps.updateFieldSetting(
							value,
							viewFieldId,
							'linkText'
						);
					} }
				/>
			</PanelRow>
		</>
	);
};

export default EntryResumeLink;
